<template>
  <view class="festival-booking-container" :style="{ paddingTop: navPaddingTop + 'px' }">
    <custom-nav-bar
      title="Á∫ø‰∏ãÂ£Æ‰π°Ëâ∫ÊúØËäÇÈ¢ÑÁ∫¶"
      gradient="linear-gradient(120deg, #e65100 0%, #ff9800 45%, #ffb74d 100%)"
    />
    
    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <view class="loading" v-if="loading">
      <text>Âä†ËΩΩ‰∏≠...</text>
    </view>
    
    <!-- Ê¥ªÂä®ÂàóË°® -->
    <view class="festival-list" v-else-if="festivals.length > 0">
      <view 
        v-for="festival in festivals" 
        :key="festival.id"
        class="festival-card"
      >
        <image :src="festival.image" class="festival-cover" mode="aspectFill"></image>
        <view class="festival-content">
          <text class="festival-title">{{ festival.title }}</text>
          <text class="festival-desc">{{ festival.description }}</text>
          <view class="festival-info">
            <view class="info-item">
              <text class="info-icon">üìÖ</text>
              <text class="info-text">{{ festival.date }} {{ festival.time }}</text>
            </view>
            <view class="info-item">
              <text class="info-icon">üìç</text>
              <text class="info-text">{{ festival.location }}</text>
            </view>
            <view class="info-item">
              <text class="info-icon">üë•</text>
              <text class="info-text" :class="{ 'sold-out': festival.availableSlots === 0 }">
                {{ festival.availableSlots === 0 ? 'Â∑≤Êª°' : `Ââ©‰Ωô ${festival.availableSlots} ‰∏™ÂêçÈ¢ù` }}
              </text>
            </view>
          </view>
          <button 
            class="book-btn"
            :class="{ disabled: festival.availableSlots === 0 }"
            :disabled="festival.availableSlots === 0"
            @click="openBookingForm(festival)"
          >
            {{ festival.availableSlots === 0 ? 'Â∑≤Êª°' : 'Á´ãÂç≥È¢ÑÁ∫¶' }}
          </button>
        </view>
      </view>
    </view>
    
    <!-- Á©∫Áä∂ÊÄÅ -->
    <view class="empty-state" v-else>
      <text class="empty-icon">üé≠</text>
      <text class="empty-text">ÊöÇÊó†Ëâ∫ÊúØËäÇÊ¥ªÂä®</text>
    </view>

    <!-- È¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó -->
    <view class="booking-modal" v-if="showBookingForm" @click.self="closeBookingForm">
      <view class="modal-content">
        <view class="modal-header">
          <text class="modal-title">È¢ÑÁ∫¶‰ø°ÊÅØ</text>
          <text class="close-btn" @click="closeBookingForm">√ó</text>
        </view>
        
        <!-- ÈÄâ‰∏≠ÁöÑÊ¥ªÂä®‰ø°ÊÅØ -->
        <view class="selected-festival-info" v-if="selectedFestival">
          <text class="festival-name">{{ selectedFestival.title }}</text>
          <text class="festival-datetime">{{ selectedFestival.date }} {{ selectedFestival.time }}</text>
          <text class="festival-place">{{ selectedFestival.location }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂßìÂêç <text class="required">*</text></text>
          <input 
            class="form-input"
            :class="{ error: formErrors.name }"
            v-model="formData.name"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÂßìÂêç"
          />
          <text class="error-text" v-if="formErrors.name">{{ formErrors.name }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÊâãÊú∫Âè∑ <text class="required">*</text></text>
          <input 
            class="form-input"
            :class="{ error: formErrors.phone }"
            v-model="formData.phone"
            type="number"
            placeholder="ËØ∑ËæìÂÖ•ÊÇ®ÁöÑÊâãÊú∫Âè∑"
          />
          <text class="error-text" v-if="formErrors.phone">{{ formErrors.phone }}</text>
        </view>
        
        <view class="form-group">
          <text class="form-label">ÂèÇ‰∏é‰∫∫Êï∞</text>
          <input 
            class="form-input"
            v-model="formData.participants"
            type="number"
            placeholder="ÈªòËÆ§1‰∫∫"
          />
        </view>
        
        <view class="form-group">
          <text class="form-label">Â§áÊ≥®</text>
          <textarea 
            class="form-textarea"
            v-model="formData.remark"
            placeholder="Â¶ÇÊúâÁâπÊÆäÈúÄÊ±ÇËØ∑Â§áÊ≥®"
          ></textarea>
        </view>
        
        <button class="submit-btn" @click="submitBooking">Á°ÆËÆ§È¢ÑÁ∫¶</button>
      </view>
    </view>
    
    <!-- È¢ÑÁ∫¶ÊàêÂäüÁ°ÆËÆ§ÂºπÁ™ó -->
    <view class="booking-modal" v-if="showConfirmation" @click.self="closeConfirmation">
      <view class="modal-content confirmation-modal">
        <view class="success-icon">‚úì</view>
        <text class="success-title">È¢ÑÁ∫¶ÊàêÂäü</text>
        <view class="confirmation-details">
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®ÂêçÁß∞</text>
            <text class="detail-value">{{ confirmationData.festivalTitle }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®Êó∂Èó¥</text>
            <text class="detail-value">{{ confirmationData.festivalDate }} {{ confirmationData.festivalTime }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">Ê¥ªÂä®Âú∞ÁÇπ</text>
            <text class="detail-value">{{ confirmationData.festivalLocation }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">È¢ÑÁ∫¶‰∫∫</text>
            <text class="detail-value">{{ confirmationData.name }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">ËÅîÁ≥ªÁîµËØù</text>
            <text class="detail-value">{{ confirmationData.phone }}</text>
          </view>
          <view class="detail-item">
            <text class="detail-label">ÂèÇ‰∏é‰∫∫Êï∞</text>
            <text class="detail-value">{{ confirmationData.participants }}‰∫∫</text>
          </view>
        </view>
        <view class="confirmation-actions">
          <button class="view-records-btn" @click="goToRecordsFromConfirm">Êü•ÁúãÊàëÁöÑÈ¢ÑÁ∫¶</button>
          <button class="close-confirm-btn" @click="closeConfirmation">ËøîÂõûÊ¥ªÂä®ÂàóË°®</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import CustomNavBar from '@/components/CustomNavBar.vue'
import ArtBookingService from '@/services/art-booking.service.js'

export default {
  components: { CustomNavBar },
  data() {
    return {
      navPaddingTop: 100,
      festivals: [],
      selectedFestival: null,
      showBookingForm: false,
      showConfirmation: false,
      loading: true,
      confirmationData: {
        festivalTitle: '',
        festivalDate: '',
        festivalTime: '',
        festivalLocation: '',
        name: '',
        phone: '',
        participants: 1
      },
      formData: {
        name: '',
        phone: '',
        participants: 1,
        remark: ''
      },
      formErrors: {}
    }
  },
  onLoad() {
    const info = uni.getSystemInfoSync()
    this.navPaddingTop = (info.statusBarHeight || 20) + 75
    this.loadFestivals()
  },
  methods: {
    /**
     * Âä†ËΩΩËâ∫ÊúØËäÇÊ¥ªÂä®ÂàóË°®
     */
    async loadFestivals() {
      this.loading = true
      try {
        this.festivals = await ArtBookingService.getFestivals()
      } catch (err) {
        console.error('Failed to load festivals:', err)
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      } finally {
        this.loading = false
      }
    },
    
    /**
     * Ë∑≥ËΩ¨Âà∞È¢ÑÁ∫¶ËÆ∞ÂΩïÈ°µÈù¢
     */
    goToRecords() {
      uni.navigateTo({
        url: '/pages/art-healing/booking-records'
      })
    },
    
    /**
     * ÊâìÂºÄÈ¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó
     * @param {Object} festival - Ëâ∫ÊúØËäÇÊ¥ªÂä®ÂØπË±°
     */
    openBookingForm(festival) {
      this.selectedFestival = festival
      this.showBookingForm = true
      this.resetForm()
    },
    
    /**
     * ÂÖ≥Èó≠È¢ÑÁ∫¶Ë°®ÂçïÂºπÁ™ó
     */
    closeBookingForm() {
      this.showBookingForm = false
      this.selectedFestival = null
      this.resetForm()
    },
    
    /**
     * ÈáçÁΩÆË°®ÂçïÊï∞ÊçÆ
     */
    resetForm() {
      this.formData = {
        name: '',
        phone: '',
        participants: 1,
        remark: ''
      }
      this.formErrors = {}
    },
    
    /**
     * È™åËØÅË°®ÂçïÊï∞ÊçÆ
     * @returns {boolean}
     */
    validateForm() {
      const result = ArtBookingService.validateArtBookingForm(this.formData)
      
      // ËΩ¨Êç¢ÈîôËØØÊ†ºÂºè
      const errors = {}
      if (!result.valid) {
        result.errors.forEach(err => {
          errors[err.field] = err.message
        })
      }
      
      this.formErrors = errors
      return result.valid
    },
    
    /**
     * ÂÖ≥Èó≠Á°ÆËÆ§ÂºπÁ™ó
     */
    closeConfirmation() {
      this.showConfirmation = false
      this.confirmationData = {
        festivalTitle: '',
        festivalDate: '',
        festivalTime: '',
        festivalLocation: '',
        name: '',
        phone: '',
        participants: 1
      }
    },
    
    /**
     * ‰ªéÁ°ÆËÆ§ÂºπÁ™óË∑≥ËΩ¨Âà∞È¢ÑÁ∫¶ËÆ∞ÂΩï
     */
    goToRecordsFromConfirm() {
      this.closeConfirmation()
      this.goToRecords()
    },
    
    /**
     * Êèê‰∫§È¢ÑÁ∫¶
     */
    async submitBooking() {
      if (!this.validateForm()) {
        return
      }
      
      if (!this.selectedFestival) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©Ê¥ªÂä®',
          icon: 'none'
        })
        return
      }
      
      const result = await ArtBookingService.createArtBooking(
        this.selectedFestival.id,
        this.selectedFestival,
        this.formData
      )
      
      if (result.success) {
        // Êõ¥Êñ∞Êú¨Âú∞Ê¥ªÂä®Ââ©‰ΩôÂêçÈ¢ù
        const festivalIndex = this.festivals.findIndex(f => f.id === this.selectedFestival.id)
        if (festivalIndex !== -1) {
          this.festivals[festivalIndex].availableSlots -= (this.formData.participants || 1)
        }
        
        // ‰øùÂ≠òÁ°ÆËÆ§‰ø°ÊÅØ
        this.confirmationData = {
          festivalTitle: this.selectedFestival.title,
          festivalDate: this.selectedFestival.date,
          festivalTime: this.selectedFestival.time,
          festivalLocation: this.selectedFestival.location,
          name: this.formData.name,
          phone: this.formData.phone,
          participants: this.formData.participants || 1
        }
        
        // ÂÖ≥Èó≠Ë°®ÂçïÔºåÊòæÁ§∫Á°ÆËÆ§ÂºπÁ™ó
        this.showBookingForm = false
        this.showConfirmation = true
        this.selectedFestival = null
        this.resetForm()
      } else {
        uni.showToast({
          title: result.message || 'È¢ÑÁ∫¶Â§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        })
      }
    }
  }
}
</script>

